/* 详情页样式 */
.detail-page {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
}

.detail-header {
  padding: 40px 60px;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  gap: 30px;
}

.back-btn {
  padding: 12px 30px;
  font-size: 20px;
  background-color: rgba(255, 255, 255, 0.9);
  color: #333;
  border: none;
  border-radius: 25px;
  
  transition: all 0.3s ease;
  font-weight: bold;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
}

.back-btn:hover {
  background-color: white;
  transform: scale(1.05);
}

.back-btn:active {
  transform: scale(0.98);
}

.detail-header h1 {
  font-size: 48px;
  color: white;
  margin: 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.detail-header p {
  font-size: 24px;
  color: rgba(255, 255, 255, 0.9);
  margin: 0;
}

.detail-content {
  flex: 1;
  padding: 60px;
  color: white;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.detail-content p {
  font-size: 24px;
  line-height: 1.8;
}

.slide-button {
  position: absolute;
  left: 50%;
  top: 970px;
  transform: translateX(-50%);
  width: 300px;
  height: 80px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  
  background-color: transparent;
  animation: breathe 3.5s ease-in-out infinite;
}

@keyframes breathe {
  0%, 100% {
    transform: translateX(-50%) scale(1);
  }
  25% {
    transform: translateX(-50%) scale(1.05);
  }
  50% {
    transform: translateX(-50%) scale(1.05);
  }
  75% {
    transform: translateX(-50%) scale(1);
  }
}

.slides-container {
  position: absolute;
  left: 620px;
  top: 207px;
  width: 1160px;
  height: 690px;
  overflow: hidden;
  overflow-x: auto;
  overflow-y: hidden;
  
  -webkit-overflow-scrolling: touch;
}

.slides-container:active {
  
}

.slides-container::-webkit-scrollbar {
  display: none;
}

.slides-container {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.slides-image {
  height: 100%;
  width: auto;
  display: block;
  user-select: none;
  -webkit-user-drag: none;
  pointer-events: none;
}